.cart{
    margin-top: 100px;
    padding-left: 20px;
    padding-right: 20px;
}
.cart-items-title{
    
    display: grid;
    grid-template-columns: .8fr 1fr 1fr 1fr 1fr 0.5fr;
    align-items: center;
    color: grey;
    font-size: max(1vw,12px);
}
.cart-items-item{
   margin: 10px;
color: black;
    
}

.cart-items-item img{
    width: 60px; 
}
.cart-items-item hr{
    height: 1px;
    background-color: #e2e2e2;
    border: none;
}
.cart-items-item .cross{
    cursor: pointer;
}
.cart-bottom{
    padding-right: 30px;
    padding-left: 40px;
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
    gap: max(12vw ,20px);
}
.cart-total{
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1;
}
.cart-totals-details{
    display: flex;
    justify-content: space-between;
    color: #555;
}
.cart-total hr{
    margin: 10px 0px;
}
.cart-total button{
    border: none;
    color: white;
    background-color: tomato;
    border-radius: 4px;
    width: max(15vw,200px);
    cursor: pointer;
    padding: 12px 0px;
}
.cart-promocode{
    flex: 1;
}
.cart-promocode p{
    color: #555;
}
.cart-promocode-input{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    border-radius: 4px;
    background-color: #eaeaea;
   
}
.cart-promocode-input input{
    border: none;
    background: transparent;
    outline: none;
    padding: 10px;
}
.cart-promocode-input button{
    width: max(15vw,150px);
    padding: 12px 5px;
    background-color: black;
    border: none;
    color: white;
    border-radius: 4px;
}
@media(max-width:750px){
    .cart-bottom{
        flex-direction: column;
    }
    .cart-promocode{
        justify-content: start;
    }
}